import { Component, OnInit } from '@angular/core';
import { ConsultationService } from '../consultation.service';
import { Rdv } from '../Model/rdv';
import { MedecinService } from '../../../services/medecin.service';

@Component({
  selector: 'app-liste-rdv',
  templateUrl: './liste-rdv.component.html',
  styleUrls: ['./liste-rdv.component.css']
})
export class ListeRdvComponent implements OnInit {
private rdvliste:Rdv;
private page:number=0;
private pages:Array<number>;

  constructor(private rdv :MedecinService) { }

  ngOnInit() {
    this.getrdvpages();
  }
  setPage(i,event:any)
  { event.preventDefault();
    this.page=i;
    this.getrdvpages();
  }
  getrdvpages()
  {
    this.rdv.listRDV(this.page).subscribe(
      res => {
        this.rdvliste= res.json();
        console.log(res);
        this.pages=new Array(this.rdvliste['totalPages']);
      },
      error => {
        console.log(error);
      }
      
    );

  }

}



