import { Component, OnInit, ViewChild, ElementRef } from '@angular/core';
import * as jspPDF from 'jspdf';
import { MedecinService } from '../../../services/medecin.service';
import { Medecin } from '../../../Model/medecin';
import { Patient } from '../../Patient/Model/patient';
import { Consultation } from '../../../Model/consultation';
@Component({
  selector: 'app-ordonnance',
  templateUrl: './ordonnance.component.html',
  styleUrls: ['./ordonnance.component.css']
})
export class OrdonnanceComponent implements OnInit {
Med:Medecin;
pat:Patient;
public date:Date;
Medecament:string;
private Consult:Consultation;
  constructor(private medecinser:MedecinService) { }

  

@ViewChild('content') content: ElementRef ;
  ngOnInit() {
    this.Medecament=this.medecinser.getMedecament();
    this.date=this.medecinser.getDate();
this.medecinser.GetMedecinConsultation().subscribe(
  res => {
    this.Med=res.json();
    console.log(this.Med);
  }
);
console.log("patient");
console.log(this.medecinser.getUsernamePatient());

this.medecinser.GetPatientByusername(this.medecinser.getUsernamePatient()).subscribe(
  res => {
    this.pat=res.json();
    console.log(this.pat);
  }
);


  }
  
  genererOrd ()
  { 
   // this.addconsultation();
    //this.medecinser.addConsultation()
    let doc = new jspPDF();
    
    let spec = {
      '#editor': function(element,renderer){
        return true;
      }
    };
    let content = this.content.nativeElement;
    doc.fromHTML(content.innerHTML,15,15, {
      'width': 190 ,
      'elementHandlers': spec

    });


   doc.save("test.pdf"); 
   // doc.autoPrint();
  }


}



