import { Component, OnInit } from '@angular/core';
import { Patient } from '../Model/patient';
import { PatientService } from '../services/patient.service';
import { Router } from '@angular/router';
import { MedecinService } from '../../../services/medecin.service';

@Component({
  selector: 'app-add-patient',
  templateUrl: './add-patient.component.html',
  styleUrls: ['./add-patient.component.css']
})
export class AddPatientComponent implements OnInit {
private patient :Patient=new Patient();
private patientadd:boolean;
private username:string;
  constructor(public medecinservice:MedecinService,private patientservice:PatientService,private routes:Router) { }

  ngOnInit() {
this.patientadd=false;
this.username=this.medecinservice.getusername();

  }

  onSubmit(dataForm){
    dataForm.username=this.username;
   this.patientservice.addPatient(dataForm).subscribe(
    
      res=> {
        this.patientadd=true;
        console.log(dataForm);
      
    }
  );

  //console.log(this.patientadd);
  this.routes.navigate(['/patient']);

    
  }

}



