import { Component, OnInit } from '@angular/core';
import { MedecinService } from '../../../services/medecin.service';
import { Medecin } from '../../../Model/medecin';
import { Patient } from '../../Patient/Model/patient';
import { Router } from '@angular/router';
import { Consultation } from '../../../Model/consultation';

@Component({
  selector: 'app-rempli-ordonnance',
  templateUrl: './rempli-ordonnance.component.html',
  styleUrls: ['./rempli-ordonnance.component.css']
})
export class RempliOrdonnanceComponent implements OnInit {
private date:Date;
private Med:Medecin= new Medecin();
private pat : Patient =new Patient();
Medecament: string;
private Consult :Consultation = new Consultation();
  constructor(private medecinser:MedecinService,private route:Router) { }
  

  sendordonnance(){
    this.medecinser.setMedecament(this.Medecament);
    console.log(this.Medecament);
    this.route.navigate(['/Ordonnace']);

  }
  ngOnInit() {
    this.date=this.medecinser.getDate();
this.medecinser.GetMedecinConsultation().subscribe(
  res => {
    this.Med=res.json();
    console.log(this.Med);
  }
);
console.log("patient");
console.log(this.medecinser.getUsernamePatient());

this.medecinser.GetPatientByusername(this.medecinser.getUsernamePatient()).subscribe(
  res => {
    this.pat=res.json();
   
      console.log("hahahahahahahahahahahaahahahahah");
    console.log(this.pat);
  }
);


  }
  sendConsultation()
  {
      this.Consult.date=this.date;
      this.Consult.description=this.Medecament;
      this.Consult.nommed=this.Med.nom;
      this.Consult.prenommed=this.Med.prenom;
      this.Consult.usernamemedecin=this.Med.username;
      this.Consult.nompat=this.pat.nom;
      this.Consult.prenompat=this.pat.prenom;
      this.Consult.usernamepatient=this.pat.username;
      console.log(this.Consult);
      this.medecinser.addConsultation(this.Consult).subscribe(
        res => {
          console.log("true");
        }
      )
  }

}



