import { Component, OnInit } from '@angular/core';
import { MedecinService } from '../../../services/medecin.service';
import { Consultation } from '../../../Model/consultation';

@Component({
  selector: 'app-consulatation-patient',
  templateUrl: './consulatation-patient.component.html',
  styleUrls: ['./consulatation-patient.component.css']
})
export class ConsulatationPatientComponent implements OnInit {
private consultation:Consultation=new Consultation();
  constructor(private consult:MedecinService) { }

  ngOnInit() {
this.consult.getConsultationPatient().subscribe(
  res => {
    this.consultation=res.json();
  } 
)
  }

}



