import { Component, OnInit } from '@angular/core';
import { MedecinService } from '../../../services/medecin.service';
import { Router } from '@angular/router';
import { Rdv } from '../Model/rdv';
import { Medecin } from '../../../Model/medecin';

@Component({
  selector: 'app-list-rdv-medecin',
  templateUrl: './list-rdv-medecin.component.html',
  styleUrls: ['./list-rdv-medecin.component.css']
})
export class ListRdvMedecinComponent implements OnInit {
private username:String;
private rdvliste:Rdv;
private rdvmodal:Rdv;
  constructor(private rdv:MedecinService, private router:Router) { }


  genererOrd (med:Rdv){
    this.rdv.setUsernamePatient(med.nom);
    console.log(med.date);   
    this.rdv.setDate(med.date);
    console.log("hatha lit3ada");
    console.log(med.nom); 
    this.router.navigate(['/RempliOrdonnance']);
  }
  sendredv(rdv:Rdv)
  {
this.rdvmodal=rdv;
console.log(this.rdvmodal);
  }
  delete()
  {
    this.rdv.removeRDV(this.rdvmodal).subscribe(
      res => {
        location.reload();
        console.log(this.rdvmodal);
      }
    )
  }
  ngOnInit() {
 this.username=this.rdv.getUsername1();
    this.rdv.listRdvPatient(this.username).subscribe(
      res => {
       this.rdvliste=res.json();
       console.log("hathaaaaaaaaaaaaaaaaa");
       console.log(this.rdvliste);
      }
    )

  }

}



