import { Component, OnInit } from '@angular/core';
import { MedecinService } from '../../services/medecin.service';
import { User } from '../../Model/User';

@Component({
  selector: 'app-list-user',
  templateUrl: './list-user.component.html',
  styleUrls: ['./list-user.component.css']
})
export class ListUserComponent implements OnInit {
private page:number=0;
private userliste:Array<any>;
private pages:Array<number>;
private modelUser:User;

  constructor(private medecinser:MedecinService) { }

  ngOnInit() {
    this.getMedecinpages();
  }

  SendUser(user:User)
  {
    this.modelUser=user;
  }

  delete()
  {
    this.medecinser.removeUser(this.modelUser.id).subscribe(
      res=> {
        location.reload();
      },error => {
        console.log("error");
      }
    )
  }
  setPage(i,event:any)
  { event.preventDefault();
    this.page=i;
    this.getMedecinpages();
}
getMedecinpages()
  {
    this.medecinser.getlistUser(this.page).subscribe(
      res => {
        this.userliste= res.json();
        console.log(res);
        this.pages=new Array(this.userliste['totalPages']);
      },
      error => {
        console.log(error);
      }
      
    );

  }
}



