import { Component, OnInit } from '@angular/core';
import { MedecinService } from '../../services/medecin.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-addrole',
  templateUrl: './addrole.component.html',
  styleUrls: ['./addrole.component.css']
})
export class AddroleComponent implements OnInit {
private username:string;
  constructor(private roleservice:MedecinService, private route:Router) { }

  ngOnInit() {
   this.username=this.roleservice.getusername();
  }
  onSubmit(dataForm)
  { console.log(dataForm);
    dataForm.username=this.username;
    this.roleservice.addrole(dataForm).subscribe(
      res => {
        if(dataForm.roleName=="Medecin")
       this.route.navigateByUrl('/add');
       else
       this.route.navigateByUrl('/addPatient');

      }
    )
  }
}



