import { Component, OnInit } from '@angular/core';
import { Patient } from '../../Patient/Model/patient';
import { PatientService } from '../../Patient/services/patient.service';
import { MedecinService } from '../../../services/medecin.service';
import { Medecin } from '../../../model/medecin';
import { Rdv } from '../Model/rdv';
import { ConsultationService } from '../consultation.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-ajouter-rdv',
  templateUrl: './ajouter-rdv.component.html',
  styleUrls: ['./ajouter-rdv.component.css']
})
export class AjouterRdvComponent implements OnInit {
private patient :Patient;
private getmedecin:Medecin;
private medecin:Medecin ;
private rdv:Rdv= new Rdv();
private RDVadd:boolean=false;
private id:number;
private addrdv:Rdv = new Rdv();
  constructor(private patientserv :PatientService,
    private medecinserv:MedecinService,private consultation:ConsultationService,private route:Router) { }

  ngOnInit() {
   //this.patient=this.patientserv.getter();
    this.medecinserv.getlistMed().subscribe(
      res=>{
        this.medecin=res.json();
        console.log(this.medecin);
      },
      error => {
        console.log(error);
      }
    );
    
   this.patient=this.patientserv.getter();
   //this.rdv.id_patient=this.patient.id;
  // this.rdv.usernamepatient=this.patient.username;
   
  //this.rdv.id_med=this.medecin.id_med;
  }


  onSubmit(dataForm)
  { 

    
    
   // console.log(this.getmedecin);
   // this.addrdv.id_patient=this.rdv.id_patient;
    this.addrdv.usernamemed=dataForm.username;
    this.addrdv.nom=this.patient.username;
    this.addrdv.date=dataForm.date;
   // this.rdv.usernamepatient=dataForm.usernamepatient;
   this.rdv.usernamemed=dataForm.username;
   // this.rdv.nom_med=this.medecin['nom'];
    //console.log("FORM");
   
    
    //console.log(this.addrdv);
    this.medecinserv.addRDV(this.addrdv).subscribe(
    
      res=> {
        this.RDVadd=true;
        console.log(this.patient);
        this.route.navigate(['/listtoutRDV']);

      
    }
  );
    
  }

}



