import { Component, OnInit } from '@angular/core';
import { PatientService } from '../services/patient.service';
import { Patient } from '../Model/patient';
import { error } from 'util';

import { Router } from '@angular/router';

@Component({
  selector: 'app-listepatient',
  templateUrl: './listepatient.component.html',
  styleUrls: ['./listepatient.component.css']
})
export class ListepatientComponent implements OnInit {
private patient:Patient[];
private patientliste:Array<any>;
private page :number=0;
private pages:Array<any>;
private patmodal:Patient;
  constructor(private patientserv:PatientService,private route:Router ) { }
  sendpat(pat:Patient)
  {
    this.patmodal=pat;
    
  }
  addMed()
{
  this.route.navigate(['/adduser']);
}
delete(){
  this.patientserv.removemed(this.patmodal).subscribe(
    res=>{
      location.reload();
      console.log(this.patmodal);
    },
    
      error => {
        console.log(error)
      }
  )

}
AjouterRDV(pat:Patient){
  this.patientserv.setter(pat);
  this.route.navigate(['/ajouterRDV']);

}

getPatientPages()
{
  this.patientserv.getlist(this.page).subscribe(
    res => {
      this.patientliste=res.json();
      this.pages=new Array(this.patientliste['totalPages']);
    }
  )
}
setPage(i,event:any)
{
  event.preventDefault();
  this.page=i;
  this.getPatientPages();
}
  ngOnInit() {
   /* this.patientserv.getlist().subscribe(
      res => {
        this.patient=res.json();

      },
     
      
    );
    console.log(this.patient);
*/

this.getPatientPages();
  }
  update(pat:Patient)

{

  this.patientserv.setter(pat);
  this.route.navigate(['/editPatient']);
}
details(med:Patient){
  this.patientserv.detailsPatient(med);
  this.route.navigate(['/detailsPatient']);
  console.log(med);
}
}



