import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import {FormsModule} from '@angular/forms';
import{RouterModule,Routes} from '@angular/router';
import { AppComponent } from './app.component';
import { HomeComponent } from './components/home/home.component';
import { TableMedecinComponent } from './components/table-medecin/table-medecin.component';
import { MedecinService } from './services/medecin.service';
import{HttpModule} from '@angular/http';
import { EditComponent } from './components/edit/edit.component';
import { AddMedecinComponent } from './components/add-medecin/add-medecin.component';
import { ListepatientComponent } from './components/Patient/listepatient/listepatient.component';
import { PatientService } from './components/Patient/services/patient.service';
import { AddPatientComponent } from './components/Patient/add-patient/add-patient.component';
import { EditPatientComponent } from './components/Patient/edit-patient/edit-patient.component';
import { DetailsComponent } from './components/Patient/details/details.component';
import { AjouterRdvComponent } from './components/Consultation/ajouter-rdv/ajouter-rdv.component';
import { RdvComponent } from './components/Consultation/Model/rdv/rdv.component';
import { ConsultationService } from './components/Consultation/consultation.service';
import { ListeRdvComponent } from './components/Consultation/liste-rdv/liste-rdv.component';
import { LoginComponent } from './login/login.component';
import { AdduserComponent } from './components/adduser/adduser.component';
import { AddroleComponent } from './components/addrole/addrole.component';
import { ListUserComponent } from './components/list-user/list-user.component';
import { ListRdvMedecinComponent } from './components/Consultation/list-rdv-medecin/list-rdv-medecin.component';
import { OrdonnanceComponent } from './components/Consultation/ordonnance/ordonnance.component';
import { RempliOrdonnanceComponent } from './components/Consultation/rempli-ordonnance/rempli-ordonnance.component';
import { ConsulatationPatientComponent } from './components/Consultation/consulatation-patient/consulatation-patient.component';

const routes:Routes=[
  {path:'',component:LoginComponent},
  {path:'home',component:HomeComponent},
  {path:'medecin',component:TableMedecinComponent},
  {path:'edit',component:EditComponent},
  {path:'add',component:AddMedecinComponent},
  {path:'patient',component:ListepatientComponent},
  {path:'addPatient',component:AddPatientComponent},
  {path:'editPatient',component:EditPatientComponent},
  {path:'detailsPatient',component:DetailsComponent},
  {path:'ajouterRDV',component:AjouterRdvComponent},
  {path:'listeRDV',component:ListRdvMedecinComponent},
  {path:'addrole',component:AddroleComponent},
  {path:'adduser',component:AdduserComponent},
  {path:'listusers',component:ListUserComponent},
  {path:'Ordonnace',component:OrdonnanceComponent},
  {path:'listtoutRDV',component:ListeRdvComponent},
  {path:'RempliOrdonnance',component:RempliOrdonnanceComponent},
  {path:'Consultation',component:ConsulatationPatientComponent}
]
@NgModule({
  declarations: [
    AppComponent,
    HomeComponent,
    TableMedecinComponent,
    EditComponent,
    AddMedecinComponent,
    ListepatientComponent,
    AddPatientComponent,
    EditPatientComponent,
    DetailsComponent,
    AjouterRdvComponent,
    RdvComponent,
    ListeRdvComponent,
    LoginComponent,
    AdduserComponent,
    AddroleComponent,
    ListUserComponent,
    ListRdvMedecinComponent,
    OrdonnanceComponent,
    RempliOrdonnanceComponent,
    ConsulatationPatientComponent
  ],
  imports: [
    BrowserModule,
    FormsModule,
    RouterModule.forRoot(routes),
    HttpModule
  ],
  providers: [ MedecinService,PatientService,ConsultationService],
  bootstrap: [AppComponent]
})
export class AppModule { }



