import { Injectable } from '@angular/core';
import { Http,Headers } from '@angular/http';
import { Rdv } from './Model/rdv';

@Injectable()
export class ConsultationService {
private rdvpat:Rdv=new Rdv();
private Pat:Rdv;
  constructor( private http :Http) { }
  addRDV(rdv:Rdv)
  { 
    let url="http://localhost:8000/addRDV";
    let headers =new Headers ({
      'Content-Type':'application/json'
    });
    
    return this.http.post(url,JSON.stringify(rdv),{headers:headers});
  }
  listRDV(page:number)
  {
    let url ="http://localhost:8000/patientconsultation";
    return this.http.get(url+'?page='+page);
  }

  setter(pat:Rdv)
 {
   this.Pat=pat;
 }
 getter()
 {
   return this.Pat;
 }
}



