import { Injectable } from '@angular/core';
import {Http,Headers} from '@angular/http';
import { Patient } from '../Model/patient';
import {JwtHelper} from 'angular2-jwt';
import { MedecinService } from '../../../services/medecin.service';

@Injectable()
export class PatientService {
private patient:Patient;
private Pat :Patient;
private patientlist:Patient[];
private patientdetails:Patient;
private jwtToken=null;
private roles:Array<any>;

  constructor(private http:Http, private MedecinSer:MedecinService) { }


 
  loadtoken()
  {
this.jwtToken=this.MedecinSer.gettoken();
  }

  
  detailsPatient(pat:Patient)
  {
    this.patientdetails=pat;
    return this.patientdetails;
  }
  getdetailspatient()
  {
    return this.patientdetails;
    
  }
  getlist(page :number)
  {

    let url='http://localhost:8080/listPatient';
    let headers =new Headers ({
      'authorization':localStorage.getItem('token')
    });
    console.log(localStorage.getItem('token'));
    
    return this.http.get(url+'?page='+page,{headers:headers});
  }
  addPatient(pa)
  { 
    let url="http://localhost:8080/addPatient";
    let headers =new Headers ({
      'Content-Type':'application/json',
      'authorization':localStorage.getItem('token')
    });
    return this.http.post(url,JSON.stringify(pa),{headers:headers});
  }
  removemed(patient :Patient)
 {
   let url ="http://localhost:8080/deletePatient";
   let headers=new Headers ({
     'Content-Type':'application/json',
     'authorization':localStorage.getItem('token')
   });
   return this.http.post(url,patient,{headers:headers});
 
 }

 updatepat(patient :Patient)
 {
   let url ="http://localhost:8080/updatePatient";
   let headers=new Headers ({
     'Content-Type':'application/json',
     'authorization':localStorage.getItem('token')

   });
   return this.http.put(url,patient,{headers:headers});
 
 }
 setter(pat:Patient)
 {
   this.Pat=pat;
 }
 getter()
 {
   return this.Pat;
 }
 send(pat:Patient[])
    {
  this.patientlist=pat;
 return this.patientlist;
 
    }
    sendedit()
    {
      return this.patientlist;
    }
}




