import { Component, OnInit } from '@angular/core';
import{Router} from '@angular/router';
import {Medecin} from '../../model/medecin';
import {MedecinService} from '../../services/medecin.service';
import { error } from 'selenium-webdriver';
@Component({
  selector: 'app-table-medecin',
  templateUrl: './table-medecin.component.html',
  styleUrls: ['./table-medecin.component.css']
})
export class TableMedecinComponent implements OnInit {
  private medecinlist:Medecin[];
  private medliste:Array<any>;
  private medmodal:Medecin;
  private page:number=0;
  private pages:Array<number>;
  private etat:number=1;
  private medetat:Medecin;
  private updated:boolean=false;
  constructor(private medecinserv:MedecinService , private router :Router) {

   }

  
   addprop(e,med:Medecin){
    if(e.target.checked)
     med.etat=1;
     else
     med.etat=0;
      this.medetat=med;
      console.log("fuction");
      console.log(this.medetat);
      this.medecinserv.updatemed(this.medetat).subscribe(
      res => {
        this.updated=true;
      });
     
    
    

   }


   removeAll(mede:Medecin)
  {
    
    this.medecinserv.removeAllPatient(mede).subscribe(
      res => {
        console.log("hello");
      }
    );
   
    this.router.navigate(['/medecin']);
    console.log("hello");
  }

  setPage(i,event:any)
  { event.preventDefault();
    this.page=i;
    this.getMedecinpages();

  
  }
  ngOnInit() {
    this.getMedecinpages();
    /*this.medecinserv.getlist().subscribe(
      res=>{
        this.medecinlist=res.json();
      },
      error => {
        console.log(error);
      }
    );*/
    
  }

  getMedecinpages()
  {
    this.medecinserv.getMedecin(this.page).subscribe(
      res => {
        this.medliste= res.json();
        console.log(res);
        this.pages=new Array(this.medliste['totalPages']);
      },
      error => {
        console.log(error);
      }
      
    );

  }

  sendmed(med:Medecin)
  {
    this.medmodal=med;
    
  }
  delete()
  {
    this.medecinserv.removemed(this.medmodal).subscribe(
      res => {
        console.log(res);
        location.reload();
      },
      error => {
        console.log(error)
      }
    );
  }
  update (med:Medecin)
{
  this.medecinserv.setter(med);
  console.log(med);
  this.router.navigate(['/edit']);
  
}
addMed()
{
  this.router.navigate(['/adduser']);
}

}



