import { Component, OnInit } from '@angular/core';
import { Medecin } from '../../model/medecin';
import { MedecinService } from '../../services/medecin.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-add-medecin',
  templateUrl: './add-medecin.component.html',
  styleUrls: ['./add-medecin.component.css']
})
export class AddMedecinComponent implements OnInit {
  private newMed:Medecin;
  private medecinAdded : boolean;
  constructor(private addMed : MedecinService,private routes:Router) { }

  ngOnInit() {  
    this.medecinAdded=false;
  }
  onSubmit(dataForm){
    
    this.addMed.addMedecin(dataForm).subscribe(
      res => {
        this.medecinAdded=true;
      },
      error =>{
        console.log(error);
      }
    );
  this.routes.navigate(['/medecin']);
  }

}





